import {  storageByLogin, } from '@/utils/common.js';

const userInfo = {
    namespaced: true,
    state: {
        token: '',
        userInfo: {},
        name: localStorage.getItem('userName'),
        avatar: '',
        roles: []
    },

    getters:{
        token: (state) => {
            return state.token;
        }
    },

    mutations: { // store 
        SET_TOKEN(state, token) {
            state.token = token;
        },
        setUserInfo(state, userInfo) {
            state.userInfo = userInfo;
        },
        SET_NAME: (state, name) => {
            state.name = name;
        },
        SET_AVATAR: (state, avatar) => {
            state.avatar = avatar;
        },
        SET_ROLES: (state, roles) => {
            state.roles = roles;
        }
    }, 

    actions: {
        //
         /*
         *token token
         *userName 
         *userPhone 
         *HosList 
         */
         LoginByQrCode({ commit },payload){
             const { userInfo,token,hosConfigArr,ssoToken,uapToken,ssoData} = payload;
             return new Promise((resolve) => {
               storageByLogin(token,userInfo,hosConfigArr,ssoToken,uapToken,ssoData)
               resolve()
             })
           },
            // 
         LogOut({ state }) {
             return new Promise((resolve, reject) => {
            
                 
             })
           },
     
         },

}

export default userInfo
