import moment  from 'moment';
export default class TimeUtil {
    constructor() {

    }


    //
   static getCurrentTime() {
   //1673250976710
     1673251561000
        return Date.now()
    }
 

    static getTimestamp() {

        let time = new Date(); // Tue Aug 28 2018 09:16:06 GMT+0800 ()
       let timestamp = Date.parse(time); // 1535419062000 Date.parse() 0
       moment(time).valueOf(); // 1535419062126 
       moment(timestamp).format(); // 2018-08-28T09:17:42+08:00
    }

     //
    static dateTimeFormate(timestamp=Date.parse(new Date),format='YYYY-MM-DD HH:mm:ss') {
        // var time = new Date(); // Tue Aug 28 2018 09:16:06 GMT+0800 ()
        // var timestamp = Date.parse(time); // 1535419062000 Date.parse() 0
        return  moment(timestamp).format(format)
    }

}